<template>
  <div>
    <v-dialog :fullscreen="autoWidth>120?null:true"  lazy persistent v-model="dialog" :width="autoWidth">
      <v-card>
        <v-toolbar >
          <v-btn icon  @click="dialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>查看订单</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn  flat @click="dialog = false">关闭</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-container fluid grid-list-lg >
          <v-layout row wrap>
            <v-flex xs12>
              <v-card color="teal" class="white--text">
                <v-card-title primary-title>
                  <div>
                    <div >
                      <div class="headline" >{{ orderInfo?orderInfo.order.guestName:'' }}</div>
                      <div class="subtitle-2">
                        订单号：{{ orderInfo?orderInfo.order.orderNo:'' }} 
                        {{ orderInfo?getText('order_ctate_code', orderInfo.order.orderStateCode):'' }}
                      </div>
                      <div class="subtitle-2">
                        渠道：{{ orderInfo?getText('channel', orderInfo.order.channelCode):'' }} 
                        <v-chip  color="green" small class="overline">
                          {{ orderInfo?getText('pay_type', orderInfo.order.payTypeCode):'' }}
                        </v-chip> 
                      </div>
                      <div class="subtitle-2">
                        创建时间：{{ orderInfo?orderInfo.order.gmtCreate:'' }} 
                      </div>
                      <div class="subtitle-2">
                        证件号：{{orderInfo?orderInfo.order.guestCertificateNo || '无' : '' }} 
                      </div>
                      <div class="subtitle-2">
                        联系电话：{{ orderInfo?orderInfo.order.guestTel || '无' : '' }} 
                      </div>
                    </div>
                  </div>
                </v-card-title>

                <v-card-text v-if="showReserve">
                  <v-list two-line subheader >
                    <v-list-tile avatar v-for="(item, index) in orderInfo.orderReserves?orderInfo.orderReserves:[]" :key="index">
                      <v-list-tile-content >
                        <v-list-tile-title >
                          预订 {{ item.roomTypeName}} {{ item.reserveNum}} 间 已分配 {{ item.allottedNum}} 间
                        </v-list-tile-title>
                        <v-list-tile-sub-title>
                          
                          <span >
                            {{ item.estimateInTime | inoutDate}} - {{ item.estimateOutTime | inoutDate}} 
                          </span>
                        </v-list-tile-sub-title>
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-list>
                
                </v-card-text>
                
                <v-card-text v-if="orderInfo && orderInfo.orderRooms && orderInfo.orderRooms.length > 0">
                  <v-list two-line subheader >
                  <!-- <v-subheader>客户</v-subheader> -->
                    <v-list-tile avatar v-for="(item, index) in orderInfo?orderInfo.orderRooms:[]" :key="index">
                      <v-list-tile-content >
                        <v-list-tile-title >{{ item.roomName}}
                          （{{ item.roomTypeName}}） 
                          {{ item.orderTypeCode === 'daily'?'日租':'钟点' }}/{{ item.roomPrice }}元 
                        </v-list-tile-title>
                        <v-list-tile-sub-title>
                          
                          <span v-if="item.orderRoomStateCode === 'live'">
                            {{ item.realInTime | inoutDate}} - {{ item.estimateOutTime | inoutDate}} 
                          </span>
                          <span v-if="item.orderRoomStateCode === 'left'">
                            {{ item.realInTime | inoutDate}} - {{ item.realOutTime | inoutDate}} 
                          </span>
                          <span v-if="item.orderRoomStateCode === 'no_live'">
                            {{ item.estimateInTime | inoutDate}} - {{ item.estimateOutTime | inoutDate}} 
                          </span>
                          {{ item.orderRoomStateCode | orderRoomState }}
                        </v-list-tile-sub-title>
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-list>
                
                </v-card-text>


                <v-card-text >
                  <v-expansion-panel >
                    <v-expansion-panel-content v-model="consumeShow">
                      <template v-slot:header >
                        <div>消费总额：{{ orderInfo?orderInfo.order.orderMoney:'' }} 元</div>
                      </template>
                      <v-card>
                        <v-card-text>
                          <v-list two-line>
                            <template v-for="(item, index) in orderConsume?orderConsume:[]" >
                              <v-divider></v-divider>
                                <v-list-tile avatar :key="index">
                                <v-list-tile-content >
                                  <v-list-tile-title >
                                    {{ item.roomName}} 
                                    {{ item.consumeItemCode === 'offset'?'冲账':getText('consume_item', item.consumeItemCode)}}
                                    （{{ item.consumeItemDetail}}） 
                                    {{ item.consumeMoney }}元 
                                  </v-list-tile-title>
                                  <v-list-tile-sub-title>
                                      {{ item.gmtCreate  | inoutDate  }} 
                                    {{ item.operatorName}}
                                  </v-list-tile-sub-title>
                                </v-list-tile-content>
                              </v-list-tile>
                            <v-divider></v-divider>
                            </template>
                          </v-list>
                        </v-card-text>
                      </v-card>
                    </v-expansion-panel-content>
                    <v-expansion-panel-content v-model="incomeShow">
                      <template v-slot:header>
                        <div>收款总额：{{ orderInfo?orderInfo.order.payMoney:'' }} 元</div>
                        <div v-if="balance>=0">余额：{{ balance }} 元</div>
                        <div v-else>欠费：{{ balance }} 元</div>
                      </template>
                      <v-card>
                        <v-card-text>
                          <v-list two-line>
                            <template v-for="(item, index) in orderIncome?orderIncome:[]" >
                              <v-divider></v-divider>
                                <v-list-tile avatar :key="index"  :color="item.incomeClassCode==='offset'?'red':''">
                                <v-list-tile-content>
                                  <v-list-tile-title >
                                    {{ item.incomeTypeCode === 'income'?'收入':'支出'}}
                                    （{{ getText('income_class', item.incomeClassCode)}}） 
                                    <span v-if="item.incomeTypeCode==='outgoings'">-</span>
                                    {{ item.incomeMoney }} 元 
                                    
                                  </v-list-tile-title>
                                  <v-list-tile-sub-title >
                                    <div>
                                    {{ item.gmtCreate | inoutDate }} 
                                    <span v-for="(pay, i) in item.payDetails" :key="'pay'+i">
                                      {{ getText('pay_method', pay.payMethodCode) }} 
                                      <span v-if="item.incomeTypeCode==='outgoings'">-</span>{{ pay.payMoney }}
                                    </span> 
                                      {{ item.operatorName }}
                                      <div>{{ item.incomeComment }}</div>
                                    </div>
                                  </v-list-tile-sub-title>
                                </v-list-tile-content>
                              </v-list-tile>
                            <v-divider></v-divider>
                            </template>
                          </v-list>
                        </v-card-text>
                      </v-card>
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                </v-card-text>
                
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { getOrderInfo, getOrderConsume, getOrderIncome, } from '../../api/order.js'
import moment from 'moment'
import { mapGetters, mapState, mapActions } from "vuex";

export default {
  name: 'ViewOrder',
  filters: {
    inoutDate(date) {
      // moment.locale('zh-CN')
      return moment(date).format('MM-DD HH:mm');
    },
    orderRoomState(code) {
      switch (code) {
        case "live":
          return "正住";
        case "left":
          return "已退";
        case "no_live":
          return "未入住";
        default:
          return "";
      }
    },
  },
  props: {
    show: Boolean,
    orderNo: String
  },
  data() {
    return {
      dialog: false,
      orderInfo: null,
      orderConsume: null,
      orderIncome: null,
      consumeShow: false,
      incomeShow: false
    }
  },
  computed: {
    balance() {
      return this.orderInfo? (this.orderInfo.order.payMoney - this.orderInfo.order.orderMoney): '';
    },
    autoWidth() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 100;
        case "sm":
          return 110;
        case "md":
          return 800;
        case "lg":
          return 900;
        case "xl":
          return 950;
      }
    },
    showReserve() {
      let show = false;
      if ( this.orderInfo &&
        this.orderInfo.orderReserves &&
        this.orderInfo.orderReserves.length > 0
      ) {
        this.orderInfo.orderReserves.forEach(element => {
          if (element.allottedNum < element.reserveNum) {
            show = true;
          }
        });
      }
      return show;
    },
    ...mapState({
      channel: state => state.dictionary.channel,
      pay_type: state => state.dictionary.pay_type,
      order_ctate_code: state => state.dictionary.order_ctate_code
    }),
    ...mapGetters([
      'getText'
    ])
  },
  watch: {
    consumeShow(consumeShow) {
      consumeShow?this.loadConsume():'';
    },
    incomeShow(incomeShow) {
      incomeShow?this.loadIncome():'';
    },
    show(show) {
      if (show) {
        this.dialog = true;
        console.log(this.orderNo);
        
      } else {

      }
    },
    dialog(dialog) {
      if (!dialog) {
        this.$emit("view-order-close");
        Object.assign(this.$data, this.$options.data());
      } else {
        this.loadOrderInfo();
      }
    },
  },
  mounted() {
    this.getDicByType('consume_item');
    this.getDicByType('income_class');
    if (!this.order_ctate_code) {
      this.getDicByType("order_ctate_code");
    }
  },
  methods: {
    loadOrderInfo() {
      getOrderInfo(this.orderNo).then(res => {
        if (res.data.code === 0) {
          this.orderInfo = res.data.data
        } else {
          this.$Msg.error(res.data.code + '：' + res.data.msg)
        }
      })
    },
    loadConsume() {
      getOrderConsume(this.orderNo).then(res => {
        if (res.data.code === 0) {
          this.orderConsume = res.data.data;
        } else {
          this.$Msg.error(res.data.code + "：" + res.data.msg);
        }
      });
    },
    loadIncome() {
      getOrderIncome(this.orderNo).then(res => {
        if (res.data.code === 0) {
          this.orderIncome = res.data.data;
        } else {
          this.$Msg.error(res.data.code + "：" + res.data.msg);
        }
      });
    },
    ...mapActions({
      getDicByType: "getDicByType"
    })
  },
}
</script>

<style>
.ss{
  primary: #1976D2;
  secondary: #424242;
  accent: #82B1FF;
  error: #FF5252;
  info: #2196F3;
  success: #4CAF50;
  warning: #FFC107;
}
</style>
